@import '~scss/variables';
@import '~scss/custom-properties';

$avatar-size: 35px;
$avatar-border-radius: 50%;

// Avatar for Users Currently Checked-In
ff-static.dash-avatar {
  flex: 0 0 $avatar-size;
  overflow: visible;
  z-index: 1;

  .ff-static.ff-static {
    padding: 0;
    overflow: visible;
  }

  img {
    //transform: scale(2) translateX(-5px);
    transition: all .2s ease;
    border-radius: $avatar-border-radius;
  }

  .ff-clickable:hover & {
    z-index: 2;

    img {
      transform: translateX(-14px) scale(1.8);
      z-index: 2;
    }
  }
}

.dash-widget-members-checked-in .ff-clickable {
  margin: 2px 0;
}

.dash-widget-admin-users-checked-in {
  .dash-avatar .fa {
    font-size: 35px;
    margin: 0 auto;
    opacity: 0.1;
  }
}
