@import '~scss/variables';
@import '~scss/custom-properties';

$avatar-size: 48px;
$avatar-border-radius: 50%;

.dash-widget-members-checked-in {
  .dash-avatar {
    flex: 0 0 $avatar-size + 2px;

    ff-img {
      height: $avatar-size;

      img {
        width: 100%;
        height: 100%;
        border-radius: $avatar-border-radius;
      }
      @supports (object-fit: cover) {
        img {
          object-fit: cover;
        }
      }
    }

    .fa {
      font-size: 48px;
      margin: 0 auto;
      opacity: 0.1;
    }
  }

  .ff-clickable:hover .dash-avatar {
    z-index: 1;

    img {
      transform: none;
      z-index: 1;
    }
  }

  .ff-clickable > ff-static:last-of-type .ff-static {
    padding: 1px 5px;
  }
}
